/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 12px;
  line-height: 1.8;
}

p {
  color: #1c1c1c;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul {
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2 {
  font-family: "DM Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1rem;
}

h3, h4 {
  margin: 0px;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

#btn-agregar_tarea {
  margin: 10px;
  padding: 10px;
  border-radius: 16px;
  width: 150px;
  height: 50px;
  background-color: #4F709C;
  border: none;
  color: #F0F0F0;
}

button:hover {
  box-shadow: inset 0 100px 0 0 #213555;
  color: white;
}

.lista-tareas ul {
  background-color: #F0F0F0;
  margin: 10px;
  padding: 10px;
  border-radius: 24px;
}
.lista-tareas ul li {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #D8D9DA;
  margin-bottom: 10px;
  padding: 10px;
  color: #1c1c1c;
  border-radius: 16px;
}
.lista-tareas ul li h4 {
  margin-left: 5px;
}
.lista-tareas li {
  box-shadow: inset 0 0 0 0 #213555;
  color: #213555;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}
.lista-tareas li:hover {
  box-shadow: inset 0 100px 0 0 #213555;
  color: white;
}

.form-agregarTarea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form-agregarTarea label, .form-agregarTarea input, .form-agregarTarea textarea, .form-agregarTarea select {
  margin: 10px;
  width: 30%;
}
.form-agregarTarea label, .form-agregarTarea input, .form-agregarTarea select {
  margin: 10px;
  width: 30%;
  height: 50px;
}
.form-agregarTarea label {
  font-size: 2rem;
}
.form-agregarTarea input {
  border-radius: 16px;
}
.form-agregarTarea .btn-form #btn-agregarTarea,
.form-agregarTarea .btn-form #btn-cancelar {
  margin: 10px;
  border-radius: 16px;
  width: 150px;
  height: 40px;
  border: none;
  color: white;
  font-size: 1.5rem;
}
.form-agregarTarea .btn-form #btn-agregarTarea {
  background-color: #4F709C;
}
.form-agregarTarea .btn-form #btn-agregarTarea :hover {
  box-shadow: inset 0 100px 0 0 #213555;
  color: white;
}
.form-agregarTarea .btn-form #btn-cancelar {
  background-color: red;
}

.form-actTarea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form-actTarea label, .form-actTarea input, .form-actTarea textarea, .form-actTarea select {
  margin: 10px;
  width: 30%;
}
.form-actTarea label, .form-actTarea input, .form-actTarea select {
  margin: 10px;
  width: 30%;
  height: 50px;
}
.form-actTarea label {
  font-size: 2rem;
}
.form-actTarea input {
  border-radius: 16px;
}
.form-actTarea .btn-form #btn-editar,
.form-actTarea .btn-form #btn-actTarea,
.form-actTarea .btn-form #btn-cancelar,
.form-actTarea .btn-form #btn-eliminar {
  margin: 10px;
  border-radius: 16px;
  width: 150px;
  height: 40px;
  border: none;
  color: white;
  font-size: 1.5rem;
}
.form-actTarea .btn-form #btn-editar {
  background-color: #4F709C;
}
.form-actTarea .btn-form #btn-actTarea, .form-actTarea .btn-form #btn-eliminar {
  background-color: #4F709C;
  display: none;
}
.form-actTarea .btn-form #btn-cancelar, .form-actTarea .btn-form #btn-eliminar {
  background-color: red;
}