html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuente_secundaria;
    font-size: 12px;
    line-height: 1.8;
}
p {
    color: $negro;
    font-size: 2rem;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

ul {
    margin: 0 auto;
    padding: 0;
    list-style-type: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2{
    font-family: $fuente_principal;
    margin: calc($separacion / 2) 0;
    font-weight: 300;
    text-align: center;   
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 2rem;
    
}
h4 {
    font-size: 1rem;
}


h3,h4{
    margin: 0px;
}